// В модели описываю структуру будущего сайта - блоки-компоненты
const model = [
  {type: 'title', value: 'Hello world from JS?'},
  {type: 'text', value: 'here we go from JS!'},
  {type: 'columns', value: [
    '1111',
    '2222',
    '3333',
    '4444',
  ]},
  {type: 'image', value: './assets/image.png'}
]

// Переменная название которой начинается на $ говорит о том, что в ней хранится DOM элемент (внегласное правило, пришло из jQuery)
const $site = document.querySelector('#site');

model.forEach(block => {
  let html = '';

  if(block.type === 'title'){
    html = title(block);
  }else if(block.type === 'text'){
    html = text(block);
  }else if(block.type === 'columns'){
    html = columns(block);
  }else if(block.type === 'image'){
    html = image(block);
  }

  $site.insertAdjacentHTML('beforeend', html);
});

function title(block){
  return `
    <div class="row">
      <div class="col-sm">
        <h1>${block.value}</h1>
      </div>
    </div>
  `
}

function text(block){
  return `
    <div class="row">
      <div class="col-sm">
        <p>${block.value}</p>
      </div>
    </div>
  `
}

function columns(block){
  const html = block.value.map(item => `<div class="col-sm">${item}</div>`).join('');
  return `
    <div class="row">
      ${html}
    </div>
  `;
}

function image(block){
  return `
    <div class="row">
      <div class="col-sm">
        <img src="${block.value}" alt=""/>
      </div>
    </div>
  `
}